@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-style: bold;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 103px;
}
@media (max-width: 1365px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page {
  scroll-behavior: smooth;
}

.btn {
  display: inline-flex;
  justify-content: center;
  margin: 0;
  padding: 10px 15px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .btn:hover, .btn:focus {
    opacity: 0.8;
  }
}

.section-title {
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
  margin: 0;
}
@media (max-width: 1365px) {
  .section-title {
    font-size: 26px;
    line-height: 39px;
  }
}
@media (max-width: 767px) {
  .section-title {
    margin-bottom: 40px;
    font-size: 24px;
    line-height: 36px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-nav {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .main-nav--nojs, .main-nav--menu-opened {
    flex-direction: column;
    padding-top: 56px;
    background-color: #f9fbfd;
    position: relative;
  }
  .main-nav--nojs::before, .main-nav--menu-opened::before {
    content: "";
    position: absolute;
    width: 42px;
    height: 22px;
    background: url("../img/svg/logo-menu-opened.svg");
    background-repeat: no-repeat;
    left: 0;
    top: 0;
  }
}

.main-nav__item {
  display: block;
}
@media (max-width: 767px) {
  .main-nav__item {
    background-color: #f9fbfd;
  }
}
.main-nav__item a {
  display: inline-block;
  vertical-align: middle;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 16px;
}
@media (max-width: 1365px) {
  .main-nav__item a {
    font-size: 14px;
    line-height: 17px;
    padding: 10px 0;
  }
}
@media (max-width: 767px) {
  .main-nav__item a {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
    padding: 9px 9px;
  }
}

.features-card {
  padding: 30px 21px 23px 30px;
  margin: 0;
  background-color: #f9fbfd;
}
@media (max-width: 1365px) {
  .features-card {
    padding: 25px 16px 20px 20px;
  }
}
.features-card img {
  margin-bottom: 18px;
}
.features-card h3 {
  line-height: 25px;
  margin-bottom: 13px;
}
@media (max-width: 1365px) {
  .features-card h3 {
    margin-bottom: 9px;
  }
}
.features-card p {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #011c40;
  margin: 0;
}

.features-card__icon {
  min-height: 50px;
}
@media (max-width: 1365px) {
  .features-card__icon {
    min-height: 46px;
  }
}

.catalog-card {
  display: grid;
  grid-template-areas: "card-area";
  width: 260px;
  min-height: 386px;
}
.catalog-card h3 {
  grid-area: card-area;
  color: #f9fbfd;
  margin: auto 30px 22px;
  padding: 0;
  z-index: 1;
}
@media (max-width: 1365px) {
  .catalog-card {
    width: 309px;
  }
  .catalog-card h3 {
    margin: auto 35px 27px;
  }
}
@media (max-width: 767px) {
  .catalog-card {
    width: 270px;
  }
  .catalog-card h3 {
    margin: auto 25px 17px;
  }
}
.catalog-card:hover {
  display: flex;
  flex-direction: column;
  background-color: #011c40;
}
.catalog-card:hover .catalog-card__image-wrapper {
  display: none;
}
.catalog-card:hover h3 {
  margin: 28px 25px 13px;
}
@media (max-width: 1365px) {
  .catalog-card:hover h3 {
    margin: 29px 35px 13px;
  }
}
@media (max-width: 767px) {
  .catalog-card:hover h3 {
    margin: 29px 25px 13px;
  }
}
.catalog-card:hover .catalog-card__description-wrapper {
  display: flex;
}
@media (max-width: 1365px) {
  .catalog-card:hover .catalog-card__description-wrapper {
    padding: 0 35px;
  }
}
@media (max-width: 767px) {
  .catalog-card:hover .catalog-card__description-wrapper {
    padding: 0 25px;
  }
}

.catalog-card__image-wrapper {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  grid-area: card-area;
}
.catalog-card__image-wrapper::before {
  content: "";
  display: block;
  position: absolute;
  background: linear-gradient(rgba(1, 28, 64, 0) 0%, #011c40 100%);
  width: 100%;
  height: 100%;
}

.catalog-card__description-wrapper {
  display: none;
  height: 100%;
  flex-wrap: wrap;
  flex-direction: column;
  padding: 0 25px 10px;
}

.catalog-card__description {
  margin: 0 0 14px;
  padding: 0;
  list-style: none;
}
.catalog-card__description dt {
  margin: 0 0 2px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #f9fbfd;
}
.catalog-card__description dd {
  margin: 0 0 10px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #f9fbfd;
}

.section {
  padding-top: 120px;
  padding-bottom: 120px;
}
@media (max-width: 1365px) {
  .section {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}
@media (max-width: 767px) {
  .section {
    padding-top: 65px;
    padding-bottom: 65px;
  }
}

.section-subtitle {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  margin: 0;
}

.form-input {
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #011c40;
  margin: 0;
  padding: 11px 16px;
  background-color: #f9fbfd;
  border: none;
}
.form-input::placeholder {
  color: rgba(1, 28, 64, 0.7);
}

.header {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 1fr 2fr;
  grid-template-areas: "logo nav" "title title";
}
@media (max-width: 1365px) {
  .header {
    grid-template-columns: 1fr 6fr;
  }
}
@media (max-width: 767px) {
  .header {
    grid-template-columns: 1fr 3fr;
  }
  .header--nojs {
    display: flex;
    flex-direction: column;
  }
  .header--nojs .header__logo, .header--menu-opened .header__logo {
    display: none;
  }
  .header--nojs .header__nav {
    display: block;
    background-color: #011c40;
    margin: 0;
  }
  .header--menu-opened::after {
    content: "";
    position: absolute;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 43, 102, 0.5);
    z-index: 0;
  }
  .header--menu-opened .header__nav {
    position: absolute;
    display: block;
    background-color: #f9fbfd;
    margin: 0;
    min-width: 285px;
    height: 100vh;
    right: 0;
    z-index: 1;
    padding: 20px 30px 0 15px;
  }
  .header--nojs .header__title-wrapper {
    position: absolute;
    bottom: 150px;
  }
  .header--nojs .header__menu-toggle {
    display: none;
  }
}

.header__image-wrapper {
  grid-row: 1/3;
  grid-column: 1/3;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  z-index: -1;
}
@media (max-width: 1365px) {
  .header__image-wrapper {
    box-shadow: none;
  }
}
.header__image-wrapper img {
  width: 100%;
}

.header__logo {
  grid-area: logo;
  margin-top: 35px;
  margin-left: 103px;
}
@media (max-width: 1365px) {
  .header__logo {
    margin-top: 30px;
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .header__logo {
    margin-top: 20px;
    margin-left: 15px;
  }
}

.header__nav {
  grid-area: nav;
  margin: 35px 90px 25px auto;
}
@media (max-width: 1365px) {
  .header__nav {
    margin: 20px 45px 20px auto;
  }
  .header__nav .main-nav__item {
    margin-right: 24px;
  }
  .header__nav .main-nav__item:last-child {
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .header__nav {
    display: none;
  }
  .header__nav .main-nav__item {
    margin-bottom: 10px;
  }
}
.header__nav a {
  color: #f9fbfd;
}

.header__menu-toggle {
  display: none;
}
@media (max-width: 767px) {
  .header__menu-toggle {
    grid-area: nav;
    padding-top: 47px;
  }
}

.header__title-wrapper {
  grid-area: title;
  color: #f9fbfd;
  margin: 53px 28px 25px 198px;
  padding: 0;
}
@media (max-width: 1365px) {
  .header__title-wrapper {
    margin: 55px 28px 25px 45px;
  }
}
@media (max-width: 767px) {
  .header__title-wrapper {
    margin: 10px 0 0 15px;
  }
}
.header__title-wrapper h1 {
  margin: 0 0 12px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  text-shadow: rgba(0, 0, 0, 0.25) 0 4px 4px;
