  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #f9fbfd;
  margin: 0 0 27px;
}
@media (max-width: 767px) {
  .booking-form p {
    line-height: 25px;
    margin-bottom: 29px;
  }
}
.booking-form input {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  margin: 0 0 20px 0;
  padding: 11px 16px;
  background-color: #f9fbfd;
  border: none;
}
.booking-form input::placeholder {
  color: rgba(1, 28, 64, 0.7);
}
.booking-form input[type=checkbox] + label {
  position: relative;
  display: inline-block;
  vertical-align: top;
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #f9fbfd;
  margin-top: 3px;
  padding-left: 23px;
}
@media (max-width: 767px) {
  .booking-form input[type=checkbox] + label {
    padding-left: 27px;
    margin: 0 auto 10px 0;
    text-align: start;
    line-height: 11px;
  }
}
.booking-form input[type=checkbox] + label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  background-color: #f9fbfd;
}
@media (max-width: 767px) {
  .booking-form input[type=checkbox] + label::before {
    top: 0;
    transform: translateY(0);
  }
}
.booking-form input[type=checkbox]:checked + label::after {
  content: "";
  background: url("../img/svg/checkbox-checked.svg") no-repeat;
  position: absolute;
  left: 3px;
  top: 3px;
  width: 18px;
  height: 18px;
}
@media (max-width: 767px) {
  .booking-form input[type=checkbox]:checked + label::after {
    left: 3px;
    top: 5px;
  }
}
.booking-form input[type=submit] {
  padding: 11px 15px;
  margin: 0;
}

.booking-form__checkbox-wrapper {
  position: relative;
